<style lang="less">
.echarts-vue {
  height: 400px;
  overflow: hidden;
}
</style>

<template>
  <div class="echarts-vue"></div>
</template>

<script>
import debounce from 'lodash.debounce';
import G from 'hey-global';
import { toRaw } from 'vue';

/** index.simple 
  require("./lib/chart/line");
  require("./lib/chart/bar");
  require("./lib/chart/pie");
  require("./lib/component/gridSimple");
 */
import echarts from 'echarts/index.simple';
import theme from './theme';

export default {
  props: {
    options: {
      type: Object,
      default: () => ({})
    },
    initOption: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.init();
  },
  watch: {
    options() {
      if (this.chart) {
        toRaw(this.chart).setOption(this.options);
      }
    }
  },
  methods: {
    init() {
      const chart = echarts.init(this.$el, theme, this.initOption);
      this.chart = chart;
      chart.setOption(this.options);

      this.resizeHanlder = debounce(
        () => {
          if (chart) {
            chart.resize();
          }
        },
        100,
        { leading: true }
      );
      window.addEventListener('resize', this.resizeHanlder);
      this.listener = G.addlistener('page_resize', this.resizeHanlder);
    }
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeHanlder);
    G.removelistener(this.listener);
    if (this.chart) {
      toRaw(this.chart).dispose();
    }
    this.chart = null;
  },
  computed: {}
};
</script>
